.App {
  text-align: center;
}

.container {
	margin: auto;
	max-width: 900px;
	width: 90%;
}
img {
	height: 100%;
	max-width: 100%;
	object-fit: cover;
}

/* width */
::-webkit-scrollbar {
	width: 30px;
  }
  .projects ::-webkit-scrollbar {
	  width: 10px;
  }
  /* Track */
  ::-webkit-scrollbar-track {
	background: #f1f1f1; 
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
	background: #3498db; 
  }
  .projects ::-webkit-scrollbar-thumb {
	background: #2980b9; 
  }
  .projects ::-webkit-scrollbar-thumb:hover {
	  background: #1e5d88;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
	background: #2980b9; 
  }


.projects {
	display: flex;
	flex-flow: row wrap;
	justify-content: space-between;
	width: 100%;
}
.project {
	border: 1px solid #000;
	border-radius: 4px; 
	-webkit-box-shadow: 0px 0px 0px 0px rgba(0,0,0,0);
	-moz-box-shadow: 0px 0px 0px 0px rgba(0,0,0,0);
	box-shadow: 0px 0px 0px 0px rgba(0,0,0,0);
	cursor: pointer;
	display: flex;
	flex-flow: column;
	margin: 1em 0;
	overflow: hidden;
	position: relative;
	height: 238px;
	width: 48%;
	transition: .3s;
}
.project:hover {
	-webkit-box-shadow: 0px 0px 7px 2px rgba(0,0,0,0.35);
	-moz-box-shadow: 0px 0px 7px 2px rgba(0,0,0,0.35);
	box-shadow: 0px 0px 7px 2px rgba(0,0,0,0.35);
	transform: scale(1.1);
}
.project:hover .overlay {
	transform: translate(0%, 0);	
}
.project .overlay {
	background: dodgerblue;
	box-sizing: border-box;
	color: #fff;
	padding: 1em;
	position: absolute;
	top: 0;
	left: 0;
	overflow-y: auto;
	height: 100%;
	width: 100%;
	transform: translate(-100%, 0);
	transition: .3s;
	z-index: 1;
}

@media (max-width: 680px) {
	.project {
		width: 100%;
	}
}

.active .project {
	transform: translateY(0);
	opacity: 1;
}
.project {
	transform: translateY(100px);
	transition: .8s;
	opacity: 0;
}

.project:nth-child(1) {
	transition-delay: 0.1s;
  }
  
  .project:nth-child(2) {
	transition-delay: 0.2s;
  }
  
  .project:nth-child(3) {
	transition-delay: 0.3s;
  }
  
  .project:nth-child(4) {
	transition-delay: 0.4s;
  }
  
  .project:nth-child(5) {
	transition-delay: 0.5s;
  }
  
  .project:nth-child(6) {
	transition-delay: 0.6s;
  }
  
  .project:nth-child(7) {
	transition-delay: 0.7s;
  }
  
  .project:nth-child(8) {
	transition-delay: 0.8s;
  }
  
  .project:nth-child(9) {
	transition-delay: 0.9s;
  }
  
  .project:nth-child(10) {
	transition-delay: 1s;
  }
  
  .project:nth-child(11) {
	transition-delay: 1.1s;
  }
  
  .project:nth-child(12) {
	transition-delay: 1.2s;	
  }
  
  